
.App {
  text-align: center;
}

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 10vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* End of Default Style */
.main-wrap{
text-align: center;
}
.wrapper{
  background-image: url("./sreerajnair.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  width: 100%;
}
.header-cont{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /*background-color: #25177b6b;
  background: linear-gradient(180deg, #25177b6b,#25177b6b, transparent);*/
  background-color: #070707;
  padding: 10px 35px;
  color:#fff;
}
.header-cont h3.About{
  color:#fff;
  text-decoration: underline;
  text-decoration-color: #fff;
}
.brand{
    color: #fff;
    text-transform: uppercase;
    font-family: fantasy;
    animation: App-logo-spin infinite 20s linear;
}
.dp{
  background-image:url("./DP.jpg");
  background-position: center;
  background-size: cover;
  width:100%;
  height:30rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
.dp *{
      margin: 0px;
    background-color: #000;
    color: #fff;
    width: 100%;
    padding: 6px 0px;
    text-align: center;
    font-weight: 400;
    background-color: #32097c91;
    text-transform: uppercase;
}
.homepage{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 700px;
  
  padding: 0px 35px;
  /*background: radial-gradient(#ffffffb0, transparent);
  width:  100%;*/
  background-color: #00000091;
  color: #fff;
}
.centered-component{
  justify-content:  center;
  align-items:  center;
}
.my-name, .my-designation{
  font-size: 70px;
  margin: 0px;
  
}
.my-name{
  color: #42ff7e;
}
.wishing{
  font-size: 50px;

}
.description{
  margin: 30px 0px;
}
.row{
  display: flex;  
  max-width:1280px;
}
.col-md-4{
  width:25%;
  margin:0 10px;
}
.col-md-8{
  width: 75%;
  margin: 0 10px;
}
.identity-tab{
  background-color:#32097c;
  color:#fff;
}
.identity-tab p, a{
color:#fff;
text-decoration: none;
font-weight:  300;
}
.abt-wrapper{
  max-width:1280px;
  margin:60px auto;
  box-shadow: 1px 1px 20px 17px #f1f1f1;

}